<template>
    <div>
        <div class="popup__header popup__header--dotted">
            <div class="title">Погасить купон</div>
            <div class="subtitle">{{coupon.action.name}}</div>
        </div>
        <div class="popup__content">
            <div class="h2">Покупатель</div>
            <div class="text-size-ml text-bold">{{ coupon.user.fio }}</div>
            
            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fix mr24" style="width: 197px; min-width: 197px; ">
                    <input-price
                            @input="showLoader()"
                            v-model="price.value"
                            @focus="removeError('price')"
                            :is-error="price.error"
                            :placeholder="'Сумма без скидки'"
                            :placeholder-input="'₽'"></input-price>
                </div>
                
                <div class="row-b__col elem-pos-fit">

                </div>

                <div v-show="this.priceCalc>0" class="row-b__col elem-pos-fix" style="width: 225px; min-width: 225px; position: relative;">
                    <div class="repay-calc">
                        <div class="repay-calc__wrap" v-if="!loader">
                            <div v-if="discountType==='percent'" class="r">Сумма скидки: <strong>{{ number_format(discountCalc) }}<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong></div>
                            <div v-else class="r">Размер скидки: <strong>{{ percentCalc }}%</strong></div>
                            <div class="r">Итого в кассу: <strong>{{ number_format(priceCalc) }}<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong></div>
                        </div>

                         <img v-else src="@/assets/img/preloader.svg" alt="">
                    </div>
                </div>
            </div>
            <!-- end row-b -->


            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fix mr24" style="width: 197px; min-width: 197px; ">
                    <input-styled  v-if="discountType==='percent'"
                                   name="percent"
                                   :value="percent.value"
                                   :mask="masks.percent"
                                   @input="showLoader()"
                                   :disabled="true"
                                   :isValidate="true"
                                   :is-error="percent.error"
                                   @focus="removeError('percent')"
                                   :placeholder="'Скидка, %'"></input-styled>
                    <input-price v-else
                                 name="discount"
                                 v-model="discount.value"
                                 @input="showLoader()"
                                 @focus="removeError('discount')"
                                 :is-error="discount.error"
                                 :placeholder="'Скидка, ₽'"
                                 :placeholder-input="'₽'"></input-price>
                </div>

                <div class="row-b__col elem-pos-fit">
                    <div class="construct-form__note">
                        Эта скидка указана в вашей акции<br>и изменить её нельзя...
                    </div>
                </div>
            </div>
            <!-- end row-b -->


            <div class="mt32">
                <textarea-field  placeholder="Напишите комментарий к погашению (необязательно)"
                                 v-model="text.value"
                                 :add-style="{height: 64 + 'px'}"
                                 @focus="removeError('text')"
                ></textarea-field>
            </div>

            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn tag="button"
                                class="btn btn--green"
                                @click.native="send()"
                                :type="buttonSend">
                        Погасить купон
                    </submit-btn>
                </div>
                <div class="row-b__col elem-pos-fit text-color-grey">
                    Внимание! Погашение купона нельзя отменить!
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'
    import {isPhone} from '@/helpers/validate.js';
    import TextareaField from '@/components/forms/TextareaField.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import InputPrice from '@/components/forms/InputPrice.vue';
    import {number_format} from '@/helpers/formatted.js';
    export default {
        name: "RepaymentRepay",
        components: {
            InputStyled,
            TextareaField,
            SubmitBtn,
            InputPrice
        },
        props: {
            coupon: {},
        },
        data: function() {
            return {
                text: {
                    value: '',
                    error: false,
                },
                price: {
                    value: '',
                    error: false,
                },
                percent: {
                    value: this.coupon.action.discount,
                    error: false,
                },
                discount: {
                    value: '',
                    error: false,
                },
                loader: false,
                buttonSend: 'normal',
                discountType: 'percent',
                timeout: false,
            }
        },
        created() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                masks: state => state.masks.items,
            }),
            discountTypeChangeText(){
                return this.discountType==='percent'?'рублях':'процентах'
            },
            discountCalc() {
                return this.discountType === 'percent'? (this.price.value.split(' ').join('') *  this.percent.value / 100).toFixed(0) : 0;
            },
            percentCalc() {
                return this.discountType !== 'percent' && this.price.value ? (this.discount.value.split(' ').join('') / this.price.value.split(' ').join('') *  100).toFixed(0) : 0;
            },
            priceCalc() {
                let price = this.price.value.split(' ').join('') - (this.discountType === 'percent'? (this.price.value.split(' ').join('') *  this.percent.value / 100).toFixed(0) : this.discount.value);
                return price > 0 ? price : 0;
            },
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.repaymentRepay.name});
        },
        methods: {
            send: function() {
                let error = false;
                this.percent.error = false;
                this.discount.error = false;
                this.price.error = false;

                if(this.discountType === 'percent' && this.percent.value.length === 0){
                    setTimeout(()=>{
                        this.percent.error = true;
                    },150);
                    error = true;
                }
                if(this.discountType !== 'percent' && this.discount.value.length === 0){
                    setTimeout(()=>{
                        this.discount.error = true;
                    },150);
                    error = true;
                }
                if(this.price.value.length === 0){
                    setTimeout(()=>{
                        this.price.error = true;
                    },150);
                    error = true;
                }
                if(!error){
                    this.buttonSend = 'load';
                    this.$store.dispatch('partnerCoupons/sendRepaymentRepay', {
                        text: this.text.value,
                        price: this.price.value.split(' ').join(''),
                        discount: this.discount.value.split(' ').join(''),
                        percent: this.percent.value,
                        id:this.coupon.id
                    }).then(()=>{
                        this.buttonSend = 'ok';
                        setTimeout(()=>{
                            this.$store.commit('popups/close', {name: this.popups.repaymentRepay.name});
                            this.buttonSend = 'normal';
                        }, 300);
                    });
                }

            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.repaymentRepay.name});
            },
            discountTypeChange(){
                if(this.discountType==='percent'){
                    this.discountType = 'sum'
                    this.percent.value = '';
                } else {
                    this.discountType = 'percent'
                    this.discount.value = '';
                }
            },
            removeError(field) {
                this[field].error = false;
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            showLoader(){
                this.loader = true;
                let self = this;
                if(this.timeout){
                    clearTimeout(this.timeout);
                }
                this.timeout = setTimeout(function(){
                    self.loader = false;
                },1000)
            }
        },

    }
</script>

<style scoped>
    .repay-calc {
        position: absolute;
        top: -34px;
        right: 0;
        width: 100%;
        height: 128px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px dotted rgba(0, 0, 0, 0.1);
        border-radius: 4px;

    }

        .repay-calc__wrap {
            padding: 16px;
        }

            .repay-calc .r {
                font-size: 12px;
                line-height: 15px;
                color: rgba(84, 84, 84, 0.5);
            }

                .repay-calc .r + .r {
                    margin-top: 4px;
                }
</style>