<template>
    <div class="inp-s-wrap full" :class="{ '--icon': isEdit || isValidate || isClearBtn, active: showLabel, 'inp-disabled': disabled }">
        <span class="inp-s-label" :class="{ error: isError }">{{ placeholder }}</span>

        <input :type="type"
            ref="field" 
            :id="inputId"
            @keyup.enter="enter()"
            @focus="setFocus(true)"
            @blur="setFocus(false)"
            class="inp-s" 
            :placeholder="showPlaceholder ? placeholderInput : ''"
            :class="{ error: isError,disabled:disabled }"
            :disabled="disabled"
            :name="nameInput"
            :value="value"
            v-mask="maskPrice"
            :autocomplete="fieldAutocomplete"
            @input="updateValue"
        >

        <span class="field-clear__btn" v-if="isClearBtn" @click="clear" >
            <span class="i i-close2"></span>
        </span>
        
        <span class="inp-s-error" v-if="isError">{{ errorText }}</span>

        <span class="inp-s-icon">
            <span class="i i-pencil2 --edit" v-if="isEdit" @click="edit"></span>
            <span class="i i-ok2" v-if="isValidate" @click="check"></span>
        </span>
        
    </div>
</template>

<script>
    import {number_format} from '@/helpers/formatted.js';

    export default {
        name: "InputPrice",
        props: {
            max: {default: 9999999},
            value: {default: ''},
            placeholder: {default: ''},
            placeholderInput: {default: ''},
            inputId: {default: ''},
            disabled: {default: false},
            name: {default: 'name'},
            isError: {default: false},
            isEdit: {default: false},
            isValidate: {default: false},
            errorText: {default: ''},
            mask: {default: ''},
            isClearBtn: {default: false},
            fieldAutocomplete: {default: 'nope'}
        },
        data: function () {
            return {
                type: 'text',
                focus: false,
                showPlaceholder: false,
                nameInput: '',
            }
        },
        computed: {
            showLabel() {
                return this.focus || this.value.toString().length > 0;
            },
            maskPrice() {
                return this.$store.getters['masks/price']
            }
        },
        mounted(){
            if(this.name !== 'name'){
                this.nameInput = this.name;
            } else {
                this.nameInput = Math.random().toString(36).substr(2, 5);
            }
        },
        methods: {
            updateValue: function (event) {
                this.$emit('input', +event.target.value < this.max ?  event.target.value : this.max.toString());
            },
            enter: function () {
                this.$emit('enter');
            },
            check: function () {
                this.$emit('check')
            },
            clear: function() {
                this.$refs.field.focus();
                this.$emit('input', '');
            },
            setFocus(value) {
                let self = this
                value = value || false;
                this.focus = value
                if(value){
                    this.$emit('focus');
                    setTimeout(function () {
                        self.showPlaceholder = true;
                    }, 50);
                } else {
                    this.$refs.field.value = this.value.trim()
                    this.$emit('blur');
                    this.showPlaceholder = false;
                }
            },
            edit: function () {
                this.$emit('edit');
            },

        }
    }
</script>

