<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-red">
            <div class="title">Аннулировать купон</div>
            <div class="subtitle">{{ coupon.action.name }}</div>
        </div>
        <div class="popup__content">
            <div class="h2 mb16">Причина аннулирования</div>
            <textarea-field  placeholder="Например, «Нет в наличии товара, ради которого покупатель оформил купон...»"
                             v-model="text.value"
                             :auto-focus="true"
                             :is-error="text.error"
                             :add-style="{height: 64 + 'px'}"
                             @focus="removeError('text')"
            ></textarea-field>

            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn tag="button"
                                class="btn btn--red"
                                @click.native="send()"
                                :type="buttonSend">
                        Аннулировать купон
                    </submit-btn>
                </div>
                <div class="row-b__col elem-pos-fit text-color-grey">
                    Внимание! Аннулирование купона нельзя отменить!
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "RepaymentAnnul",
        components: {
            TextareaField,
            SubmitBtn
        },
        props: {
            coupon: {}
        },
        data: function() {
            return {
                text: {
                    value: '',
                    error: false,
                },
                buttonSend: 'normal'
            }
        },
        created() {

        },
        methods: {
            send() {
                this.text.error = false;
                if(this.text.value.length < 10) {
                    setTimeout(()=>{
                        this.text.error = true;
                    },150);
                } else {
                    this.buttonSend = 'load';
                    this.$store.dispatch('partnerCoupons/sendRepaymentAnnul', {
                        text: this.text.value,
                        id:this.coupon.id
                    }).then(()=>{
                        this.buttonSend = 'ok';
                        setTimeout(()=>{
                            this.$store.commit('popups/close', {name: this.popups.repaymentAnnul.name});
                            this.buttonSend = 'normal';
                        }, 300);
                    });
                }

            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.repaymentAnnul.name});
            },
            removeError(field){
                this[field].error = false;
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                masks: state => state.masks.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.repaymentAnnul.name});
        }
    }
</script>

