<template>
    <div class="coupon-stat">
        <div class="coupon-stat__wrap">
            <div class="coupon" :id="'fly'+item.id">
                <tooltip
                        :direction="'left-center-direction'"
                        :add-class-wrap="'coupon__bookmark-wrap'"
                        :add-style="{width: 248 + 'px', 'top': '67px', 'right': '126px'}">
                    <div slot="tooltipBtn" class="coupon__bookmark">
                        <div class="wrap">
                            <span class="d">−{{ item.action.discount }}%</span>
                            <span class="p">
                                ≈{{ item.action.realSavings }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                            </span>
                        </div>
                    </div>

                    <div slot="tooltipContent" style="font-size: 16px; ">
                        При обращении к этому<br>
                        поставщику средний чек<br>
                        составляет <strong>{{ item.action.averageCheckCalc }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                        а экономия – около
                        <strong>{{ item.action.realSavings }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                    </div>
                </tooltip>
                <div class="coupon__wrap">
                    <div class="coupon__img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.action.image+')' }">
                        <div class="coupon__user-info" v-if="$route.name == 'partnerCouponsWork'">
                            Покупатель <br>
                            <b>{{ item.user.fio }}</b> <br>
                            {{ item.user.phone }}
                        </div>
                    </div>

                    <div class="coupon__content">
                        <div class="coupon__favorite">
                            <a target="_blank" :href="'/id'+item.action.id" class="i i-link --lnk"></a>
                        </div>

                        <div class="coupon__stat">
                            <span class="elem --views">
                                <span class="i i-eye"></span>
                                <span class="t">{{ item.action.showCounterFormat }}</span>
                            </span>

                            <span class="elem --buy" v-if="item.action.statistics.extinguished > 0">
                                <span class="i i-cart"></span>
                                <span class="t">{{ item.action.statistics.extinguished }}</span>
                            </span>

                            <!--<span class="elem &#45;&#45;comments" v-if="+item.action.countOpinions > 0">
                               <span class="i i-comment"></span>
                               <span class="t">{{ item.action.countOpinionsFormat }}</span>
                           </span>-->
                        </div>

                        <div class="coupon__name">
                            {{ item.action.name }}
                        </div>

                        <div class="coupon__company">{{ item.action.provider.name }}</div>

                        <div class="w">
                            <div class="coupon__economy">
                                <div class="t" v-if="item.status.code !== 'extinguished'">
                                    Экономия

                                    <tooltip :direction="'bottom-right-direction'"
                                             :add-class-wrap="'dib mt-2'"
                                             :add-style="{width: 376 + 'px'}">
                                        <span slot="tooltipBtn" class="i i-question3 icon-btn"></span>

                                        <div slot="tooltipContent">
                                            <p><strong>Как рассчитывается экономия? Очень просто:</strong></p>

                                            <ul class="ul-disc-small">
                                                <li>Средний чек у данного поставщика составляет {{ item.action.averageCheckCalc }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                                <li>Предоставляемая скидка составляет {{ item.action.discount }}%</li>
                                                <li>{{ item.action.discount }}% от {{ item.action.averageCheckCalc }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> – это примерно {{ item.action.realSavings }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                            </ul>

                                            <p>То есть, при обращении к данному поставщику экономия обычно составляет ≈{{ item.action.realSavings }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></p>
                                        </div>
                                    </tooltip>
                                </div>

                                <div class="t" v-else>
                                    Сэкономлено
                                </div>
                                <div class="p" v-if="item.status.code === 'extinguished'">
                                    {{ item.discount.sumFormat }}
                                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                </div>
                                <div class="p" v-else>
                                    {{ item.action.realSavings }}
                                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                </div>
                            </div>

                            <div class="coupon__info" v-if="item.status.code !== 'extinguished'">
                                Средний чек
                                {{ item.action.averageCheckCalc }}
                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,

                                скидка
                                <span class="n" >{{ item.action.discount }}%</span>
                            </div>
                            <div v-else class="coupon__info">
                                Сумма покупки
                                {{ item.priceFormat }}
                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,

                                скидка
                                <span class="n">{{ item.discount.percent }}%</span>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- end coupon__wrap -->
            </div>
            <!-- end coupon -->
        </div>
        <!-- end coupon-stat__wrap -->



        <div class="coupon-stat__control">
            <div class="stat-icon" v-if="item.status.code === 'valid'">Активен {{ item.status.dateRemain }}</div>
            <div class="stat-icon" v-if="item.status.code === 'extinguished'">Использован {{ item.status.date }}</div>
            <div class="stat-icon" v-if="item.status.code === 'expired'">Просрочен {{ item.status.date }}</div>
            <div class="stat-icon" v-if="item.status.code === 'canceled'">Аннулирован {{ item.status.date }}</div>
            <div class="stat-icon" v-if="item.status.code === 'withdrawn'">Отменён {{ item.status.date }}</div>

            <div class="link">
                <div class="stat-info">
                    <span class="w">
                        <span class="i i-marker text-color-grey"></span>
                    </span>
                    <span class="t text-size-m text-color">{{ item.city }}</span>
                </div>
            </div>

            <div class="link">
                <a href="javascript:void(0);" @click="openCouponHistory(item)" class="link-icon">
                    <span class="w">
                        <span class="i i-info"></span>
                    </span>
                    <span class="t">Посмотреть историю</span>
                </a>
            </div>

            <div class="link" v-if="item.status.code === 'valid'">
                <a href="javascript:void(0);" @click="openRepaymentAnnul(item)" class="link-icon">
                    <span class="w">
                        <span class="i i-close2"></span>
                    </span>
                    <span class="t">Аннулировать</span>
                </a>
            </div>

            <div class="link mt24 mb24" v-if="item.status.code === 'valid'">
                <a href="javascript:void(0);" @click="openRepaymentRepay(item)" class="btn btn--green btn--ico full">
                    <span class="w">
                        <span class="i i-ok i--white" style="transform: scale(1.25)"></span>
                    </span>
                    <span class="t text-size-l text-bold">Погасить купон</span>
                </a>
            </div>



            <div class="link" v-if="item.status.code === 'valid' || item.inWork">
                <input :checked="item.inWork"  type="checkbox">
                <label @click="changeWork(item.id)" :class="{'inWork': item.inWork}">
                Купон в работе
            </label>
            </div>
        </div>
        <!-- end coupon-stat__control -->
    </div>
    <!-- end coupon-stat -->
</template>

<script>
    import Popup from '@/components/modal/Popup.vue';
    import {flyToFavorite} from '@/helpers/animate';
    import {flyToWork} from '@/helpers/animate';
    import CouponCancel from '@/components/modal/CouponCancel.vue';
    import CouponClaim from '@/components/modal/CouponClaim.vue';
    import RepeatSendInfo from '@/components/modal/RepeatSendInfo.vue';
    import CheckoutFail from '@/components/modal/CheckoutFail.vue';
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';

    export default {
        name: "RepaymentListItem",
        components: {
            Popup,
            CouponCancel,
            CouponClaim,
            RepeatSendInfo,
            CheckoutFail,
            Tooltip,
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {
            changeWork(id){
                console.log(id);
                this.$store.dispatch('partnerCoupons/changeWork', {id:id});
                let item = document.querySelector('#fly' + id);
                if(this.item.inWork){
                    flyToWork(item);
                }
            },
            openRepaymentAnnul: function (item) {
                this.$store.dispatch('popups/open', {name: this.popups.repaymentAnnul.name});
                this.$store.commit('partnerCoupons/setCurrent', item)

            },
            openRepaymentRepay: function (item) {
                this.$store.dispatch('popups/open', {name: this.popups.repaymentRepay.name});
                this.$store.commit('partnerCoupons/setCurrent', item)
            },
            openCouponHistory: function (item) {
                this.$store.dispatch('popups/open', {name: this.popups.couponHistory.name});
                this.$store.commit('partnerCoupons/setCurrent', item)
            }
        }
    }

</script>
<style>
    .coupon__user-info{
        background: #FFFFFF;
        box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
        border-radius: 4px;
        padding: 4px 8px;
        color: #202020;
        position: absolute;
        top: 8px;
        left: 8px;
        font-size: 12px;
        line-height: 15px;
    }
</style>
